a {
  text-decoration: none;
  color: white;
}

a:hover {
  color: white;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 15s linear;
  }
}

.mob_alert {
  width: 100%;
  height: 100vh;
  background-color: rgba(58, 58, 58, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50000000;
}

.mob_alert h3 {
  width: 80%;
  padding: 40px 20px;
  box-sizing: border-box;
  font-size: 1.1rem;
  margin: 0 auto;
  background-color: rgb(124, 53, 172);
  border-radius: 30px;
  position: relative;
  top: 36%;
  color: white;
  border: 1px solid rgb(224, 222, 222);
}

#jinwrap {
  width: 100%;
  padding-bottom: 150px;
  overflow: hidden;
}

.bacground {
  width: 100%;
  height: 500px;

  background-size: cover;
  position: relative;
}

.bg1 {
  background-image: url(./aesset/mlb_b.png);
}
.bg2 {
  background-image: url(./aesset/mlb_b2.png);
}
.bg3 {
  background-image: url(./aesset/mlb_b3.png);
}
.bg4 {
  background-image: url(./aesset/mlb_b4.png);
}
.navjin {
  width: 100% !important;
  box-sizing: border-box;
  background-color: rgb(67, 26, 114) !important;
}

.bacground h3 {
  width: 100%;
  position: absolute;
  top: 47%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.8rem;
  color: rgb(92, 27, 136);
  font-weight: bold;
}

.new {
  width: 100%;
  margin-top: 50px;
  margin-bottom: 40px;
  font-weight: 400;
  position: relative;
}

.new .sort_b {
  display: inline-block;
  width: 50px;
  font-size: 0.7rem;
  line-height: 27px;
  position: absolute;
  top: 0;
  right: 15px;
  background: rgb(140, 63, 192);
  color: white;
  font-weight: 500;
  border: none;
  border-radius: 10px;
}

.new .sort_b:hover {
  background: white;
  color: rgb(140, 63, 192);
  line-height: 25px;
  font-weight: 600;
  border: 1px solid rgb(140, 63, 192);
}

.cont2 {
  width: 100%;
  padding: 0 10px;
  padding-bottom: 30px !important;
  box-sizing: border-box;
}

.cont2 .jinrow {
  margin: 0 auto;
  width: 95% !important;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.jinrow .mlb_col {
  width: 32%;
  text-align: center;
}

.jinrow .mlb_col {
  text-align: center;
  margin-bottom: 32px;
}

.mlb_col .square {
  width: 100%;
  margin-top: 16px;
  padding: 0 2px;
  box-sizing: border-box;
}

.jinrow .mlb_col h4 {
  text-align: center;
  color: #282c34;
  font-size: 0.92rem;
  font-weight: bold;
}

.jinrow .mlb_col p {
  color: #282c34;
  font-size: 0.79rem;
  font-weight: 500;
  margin: 0;
}

#jinhee {
  width: 100%;
  display: flex;
  position: relative;
}

#jinhee #tourbox {
  width: 100%;
  height: 400px;
  padding: 30px 7px;
  box-sizing: border-box;
  position: absolute;
  top: 5px;
  overflow: hidden;
  transition: cubic-bezier(0.05, 0.71, 0.68, 0.18) 0.7s;
  opacity: 0;
  transform: translateY(15px);
}

.onbox {
  opacity: 1 !important;
  transform: translateY(0) !important;
}

#tourbox .korea_j {
  width: 100%;
  box-sizing: border-box;
}

/* .tourbox .korea_j .artbox {
  width: 185px;
 
} */

.korea_j .slick-slide {
  padding-left: 10px;
}

.artbox .artimg {
  width: 100%;
  height: 100px;
  border-radius: 15px;
  background: rgb(255, 255, 255);
  box-shadow: 5px 5px 7px rgba(78, 77, 77, 0.4);
}

.artbox p {
  width: 100%;
  height: 50px;
  position: relative;
  right: -15px;
  background: rgb(207, 167, 226);
  border-radius: 15px;
  margin-top: 10px;
  box-shadow: 5px 5px 7px rgba(73, 73, 73, 0.4);
}

#cont3 {
  width: 50%;
  background: rgb(145, 82, 187);
  height: 400px;
  box-shadow: 10px 10px 7px rgba(61, 22, 22, 0.35);
  transition: cubic-bezier(1, 0.27, 0.25, 1) 0.35s;
}

#cont3:nth-child(1) {
  border-radius: 30px 0 0 30px;
  transform: translateX(-110%);
}
#cont3:nth-child(2) {
  border-radius: 0 30px 30px 0;
  transform: translateX(110%);
}
.on {
  transform: translateX(0) !important;
}
