@media all and (max-width: 750px) {
  .cont2 {
    width: 100%;
    margin-top: 35px;
    padding: 0 10px;
    box-sizing: border-box;
  }
  .cont2 .jinrow {
    margin: 0 auto;
    width: 100% !important;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .jinrow .mlb_col {
    width: 49.2%;
    text-align: center;
  }
}

.fail {
  width: 83%;
  height: 60px;
  background: rgba(155, 152, 152, 0.2);
  line-height: 60px;
  margin: 35px auto;
  text-align: center;
  color: rgb(92, 35, 156);
  font-size: 1.05rem;
  border-radius: 15px;
  box-shadow: 5px 6px 4px rgba(66, 66, 66, 0.2);
}

.spinner_j {
  position: relative;
  top: 5px;
  margin-bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.more {
  width: 83%;
  margin: 30px auto;
  background: white;
  line-height: 35px;
  border-radius: 10px;
  border: 1.75px solid rgb(82, 32, 138);
  color: rgb(67, 26, 114);
}

.more:hover {
  background: rgb(98, 48, 155);
  color: white;
  border-color: white;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nct_j {
  padding-bottom: 250px;
  box-sizing: border-box;
}

.prod img {
  margin-top: 20px;
}

.mt-4 {
  text-align: left;
  padding: 0 30px;
  padding-bottom: 25px;
  box-sizing: border-box;
}

.pt-5 {
  width: 100%;
  padding: 0 !important;
  line-height: 30px;
  position: relative;
}
.pt-5 .hart {
  display: inline-block;
  position: absolute;
  right: 25px;
}

.buybox {
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  padding: 0;
  z-index: 5555555555555;
  display: flex;
}

.buybox .btnjin {
  width: 50%;
  line-height: 40px;
  border: none;
  font-size: 1.1rem;
}

.buybox .btnjin:nth-child(1) {
  background: rgb(34, 34, 34);
}
.buybox .btnjin:nth-child(2) {
  background: rgb(98, 48, 155);
}
.aleat_t {
  width: 85%;
  background: rgb(170, 86, 170);
  padding: 14px;
  box-sizing: border-box;
  margin: 45px auto;
  font-size: 1rem;
  font-weight: bold;
  color: white;
  border-radius: 50px;
}
.popup {
  width: 100%;
  padding: 40px 0;
  text-align: left !important;
  padding-bottom: 20px;
  background: rgb(238, 220, 250);
  position: fixed;
  left: 0;
  bottom: 50px;
  z-index: 666666666666666;
  border-radius: 50px 50px 0 0;
  border: 1.8px solid rgb(92, 27, 136);
}

.popup select {
  background: white;
  width: 87%;
  height: 40px;
  line-height: 60px;
  color: rgb(45, 11, 68);
  border: 1.7px solid rgb(92, 27, 136);
  border-radius: 10px;
  display: block;
  margin: 0 auto;
  margin-bottom: 7px;
}

.popup .total {
  width: 87%;
  display: flex;
  margin: 0 auto;
  margin-top: 18px;
}

.popup .total h3 {
  width: 50%;
  font-size: 1.4rem;
}

.popup .total h3:nth-child(2) {
  text-align: right;
  display: flex;
}
.popup .total h3:nth-child(2) span {
  display: block;
  line-height: 24px;
}
.popup .total h3:nth-child(2) .s1 {
  width: 25%;
  text-align: left;
  font-size: 1.02rem;
  font-weight: bold;
  color: rgb(92, 27, 136);
}
.popup .total h3:nth-child(2) .s2 {
  width: 75%;
  text-align: right;
}
.nav-pills {
  width: 100%;
  margin-top: 10px;
  flex-wrap: nowrap;
  border-bottom: 1.2px dashed rgba(107, 33, 156, 0.5);
  box-sizing: border-box;
}
.nav-link {
  padding: 8px 10px !important;
  box-sizing: border-box !important;
  color: rgb(140, 63, 192) !important;
  transition: cubic-bezier(0.42, 0, 0.1, 1.13) 0.35s !important;
}
.nav-item {
  width: 25%;
  box-sizing: border-box;
}

.tab_con {
  width: 100%;
  margin-top: 25px;
}

.active {
  color: white !important;
  background: rgb(140, 63, 192) !important;
}

.wow-enter {
  opacity: 0;
  transform: scale(1.15);
}

.wow-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 400ms;
}

