.slick-dots {
  width: 85% !important;
  bottom: 12px !important;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000000 !important;
  height: 28px;
}

.page_ani-enter {
  transform: translate3d(100%, 0, 0);
}

.page_ani-enter-active {
  transform: translate3d(0, 0, 0);
  transition: all 500ms;
}

