.joinwrap {
  width: 100%;
  height: 95vh;
  position: relative;
  background: linear-gradient(
    to bottom right,
    rgb(63, 19, 112),
    rgb(121, 73, 177)
  );
}

.log_jh {
  width: 150px;
  color: white;
  font-size: 2.2rem;
  font-weight: bold;
  margin: 0 auto;
  margin-bottom: 50px;
  animation: login infinite 1.8s cubic-bezier(0, 0, 0.17, 0.64);
  /* text-shadow: 3px 3px 4px rgba(46, 45, 45, 0.85); */
}

.joinwrap .joinbox {
  width: 88%;
  text-align: center;
  position: relative;
  top: 46%;
  margin: 0 auto;
  transform: translateY(-50%);
  border-radius: 10px;
  padding: 30px 0;
  box-sizing: border-box;
}

/* @keyframes login {
  from {
    transform: rotateY(0);
  }
  to {
    transform: rotateY(180deg);
  }
} */

.joinbox .info {
  display: inline-block;
  width: 90%;
  height: 50px;
  margin: 16px auto;
  line-height: 50px;
  border-radius: 50px;
  padding-left: 18px;
  background: rgba(204, 201, 201, 0.26);
  border: 1.2px solid rgb(216, 215, 215);
  transition: all 0.35s;
}

.joinbox input:hover {
  box-shadow: 0 0 4px 9px rgba(164, 196, 245, 0.2);
}

.joinbox input::placeholder {
  color: rgba(241, 235, 235, 0.75);
  font-size: 1.1rem;
}

.joinwrap .joinbox .sendbox {
  width: 100%;
  margin-top: 50px;
}

.joinbox .sendbox .check {
  width: 56%;
  line-height: 17px;
  margin-right: auto;
  margin-bottom: 18px;
}
.sendbox .check input {
  display: inline-block;
  width: 17px;
  height: 17px;
  background: rgba(77, 132, 235, 0.3);
  margin-right: 10px;
}
.sendbox .check input:checked {
  background: rgb(131, 12, 131);
}

.sendbox .check span {
  display: inline-block;
  color: white;
  height: 17px;
  line-height: 17px;
  position: relative;
  top: -3px;
  font-size: 0.9rem;
}
.sendbox .log_button {
  display: block;
  width: 90%;
  height: 50px;
  color: white;
  font-size: 1.3rem;
  letter-spacing: 3px;
  margin: 0 auto;
  font-weight: bold;
  line-height: 50px;
  border-radius: 50px;
  background: rgb(137, 34, 207);
  background: linear-gradient(
    35deg,
    rgb(171, 66, 241) 22%,
    rgb(236, 61, 195) 70%,
    rgb(247, 123, 177) 97%
  );
  border: none;
  box-shadow: 3px 2px 4px rgba(46, 45, 45, 0.75);
}

.sendbox .log_button:nth-child(1) {
  margin-bottom: 15px;
}
.sendbox .log_button:nth-child(2) {
  font-size: 1.1rem;
}

.vip {
  width: 100%;
  height: 100%;
  background: rgba(71, 18, 107, 0.75);
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 66666666666;
}

.vip h2 {
  width: 75%;
  line-height: 80px;
  background: rgb(233, 233, 233);
  color: rgb(71, 18, 107);
  font-weight: 600;
  font-size: 1.06rem;
  border-radius: 15px;
  border: 1.4px solid white;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

